import Swiper from 'swiper';
import { Navigation } from 'swiper/modules';
import 'swiper/css';

new Swiper('.approach_swiper', {
  modules: [Navigation],
  loop: true,
  allowSlidePrev: true,
  slideToClickedSlide: true,
  centeredSlides: true,
  centerInsufficientSlides: true,
  slidesPerView: 0,
  spaceBetween: 60,
  speed: 1000,
  navigation: {
    nextEl: '.arrow_right',
    prevEl: '.arrow_left',
  }
})

new Swiper('.testimonials_swiper', {
  loop: true,
  allowSlidePrev: true,
  slideToClickedSlide: true,
  centeredSlides: true,
  centerInsufficientSlides: true,
  slidesPerView: 0,
  spaceBetween: 60,
  speed: 1000,
})